.news_app {
  display: flex;
  flex-direction: column;
}

.news_app .dropdowns_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

.news_app .grid_cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(300px, 100%), 1fr));
  gap: 20px;
  padding: 20px;
}

.news_app .card {
  flex: 1;
  margin-top: 10px;
}

.news_app .link {
  text-decoration: none;
  color: black;
}

.chips_container {
  text-align: center;
}

.display_flex {
  display: flex;
  margin-top: 4px;
}
